// components/Chat.jsx

import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Avatar } from "@mui/material";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth, db } from "../firebase";
import getRecipientEmail from "../utils/getRecipientEmail";
import { useRouter } from "next/router";
import { 
  collection, 
  query, 
  where, 
  getDocs, 
  orderBy, 
  limit 
} from "firebase/firestore";
import { useCollection } from "react-firebase-hooks/firestore";
import MessageStatus from "./ChatScreen/components/MessageStatus";
import { MESSAGE_STATUS } from "./ChatScreen/constants";
// components/Chat.jsx - Replace the useEffect with real-time listener

import { onSnapshot } from "firebase/firestore";

useEffect(() => {
  if (!id || !user) return;

  // Real-time listener for latest message
  const messagesRef = collection(db, "chats", id, "messages");
  const q = query(messagesRef, orderBy("timestamp", "desc"), limit(1));
  
  const unsubscribeMessages = onSnapshot(q, (snapshot) => {
    if (!snapshot.empty) {
      const messageData = snapshot.docs[0].data();
      setLatestMessage({
        id: snapshot.docs[0].id,
        ...messageData,
        timestamp: messageData.timestamp?.toMillis(),
      });
    } else {
      setLatestMessage(null);
    }
  });

  // Real-time listener for unread count
  const unreadQuery = query(
    messagesRef,
    where("user", "==", recipientEmail),
    where("status", "in", [MESSAGE_STATUS.SENT, MESSAGE_STATUS.DELIVERED])
  );
  
  const unsubscribeUnread = onSnapshot(unreadQuery, (snapshot) => {
    setUnreadCount(snapshot.docs.length);
  });

  return () => {
    unsubscribeMessages();
    unsubscribeUnread();
  };
}, [id, user, recipientEmail]);
function Chat({ id, users }) {
  const router = useRouter();
  const [user] = useAuthState(auth);
  const [latestMessage, setLatestMessage] = useState(null);
  const [unreadCount, setUnreadCount] = useState(0);

  const recipientEmail = getRecipientEmail(users, user);

  // Get recipient info
  const recipientQuery = recipientEmail
    ? query(collection(db, "users"), where("email", "==", recipientEmail))
    : null;
  const [recipientSnapshot] = useCollection(recipientQuery);
  const recipient = recipientSnapshot?.docs?.[0]?.data();

  // Fetch latest message and unread count
  useEffect(() => {
    if (!id || !user) return;

    const fetchLatestMessage = async () => {
      try {
        const messagesRef = collection(db, "chats", id, "messages");
        const q = query(messagesRef, orderBy("timestamp", "desc"), limit(1));
        const snapshot = await getDocs(q);

        if (!snapshot.empty) {
          const messageData = snapshot.docs[0].data();
          setLatestMessage({
            id: snapshot.docs[0].id,
            ...messageData,
            timestamp: messageData.timestamp?.toMillis(),
          });
        } else {
          setLatestMessage(null);
        }
      } catch (error) {
        console.error("Error fetching latest message:", error);
      }
    };

    const fetchUnreadCount = async () => {
      try {
        const messagesRef = collection(db, "chats", id, "messages");
        
        // Get messages sent by recipient that are not read by current user
        const unreadQuery = query(
          messagesRef,
          where("user", "==", recipientEmail),
          where("status", "in", [MESSAGE_STATUS.SENT, MESSAGE_STATUS.DELIVERED])
        );
        
        const snapshot = await getDocs(unreadQuery);
        setUnreadCount(snapshot.docs.length);
      } catch (error) {
        console.error("Error fetching unread count:", error);
        setUnreadCount(0);
      }
    };

    fetchLatestMessage();
    fetchUnreadCount();

    // Refresh every 5 seconds to keep messages updated
    const interval = setInterval(() => {
      fetchLatestMessage();
      fetchUnreadCount();
    }, 5000);

    return () => clearInterval(interval);
  }, [id, user, recipientEmail]);

  const enterChat = () => {
    router.push(`/chat/${id}`);
  };

  // Format timestamp
  const formatTimestamp = (timestamp) => {
    if (!timestamp) return "";
    
    const messageDate = new Date(timestamp);
    const today = new Date();
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);

    // Check if today
    if (messageDate.toDateString() === today.toDateString()) {
      return messageDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    }
    
    // Check if yesterday
    if (messageDate.toDateString() === yesterday.toDateString()) {
      return "Yesterday";
    }
    
    // Check if within this week
    const weekAgo = new Date(today);
    weekAgo.setDate(weekAgo.getDate() - 7);
    if (messageDate > weekAgo) {
      return messageDate.toLocaleDateString([], { weekday: 'short' });
    }
    
    // Older messages
    return messageDate.toLocaleDateString([], { month: 'short', day: 'numeric' });
  };

  // Truncate message
  const truncateMessage = (message, maxLength = 30) => {
    if (!message) return "";
    if (message.length <= maxLength) return message;
    return message.substring(0, maxLength) + "...";
  };

  // Get preview text for the message
  const getMessagePreview = () => {
    if (!latestMessage) return "No messages yet";

    // Voice message
    if (latestMessage.voiceURL) {
      return "🎤 Voice message";
    }

    // File attachment
    if (latestMessage.fileURL) {
      if (latestMessage.fileType?.startsWith('image/')) {
        return "📷 Photo";
      }
      if (latestMessage.fileType?.startsWith('video/')) {
        return "🎥 Video";
      }
      return `📎 ${latestMessage.fileName || 'File'}`;
    }

    // Regular text message
    return truncateMessage(latestMessage.message);
  };

  const isOwnMessage = latestMessage?.user === user?.email;

  return (
    <Container onClick={enterChat}>
      <UserAvatar src={recipient?.photoURL} />
      <ChatContent>
        <ChatHeader>
          <RecipientName>
            {recipientEmail === user?.email ? "You (Notes)" : recipientEmail}
          </RecipientName>
          {latestMessage && (
            <MessageTime>{formatTimestamp(latestMessage.timestamp)}</MessageTime>
          )}
        </ChatHeader>
        <LastMessageContainer>
          <LastMessage isOwnMessage={isOwnMessage}>
            {isOwnMessage && latestMessage && (
              <MessageStatus 
                status={latestMessage.status || MESSAGE_STATUS.SENT}
                darkMode={false}
              />
            )}
            <MessageText hasUnread={unreadCount > 0 && !isOwnMessage}>
              {getMessagePreview()}
            </MessageText>
          </LastMessage>
          {unreadCount > 0 && !isOwnMessage && (
            <UnreadBadge>{unreadCount}</UnreadBadge>
          )}
        </LastMessageContainer>
      </ChatContent>
    </Container>
  );
}

export default Chat;
// Update Chat.jsx styled components for dark mode

const Container = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 15px;
  word-break: break-word;
  transition: background-color 0.2s;

  :hover {
    background-color: ${props => props.theme.darkMode ? '#2a2a2a' : '#f5f5f5'};
  }
`;

const RecipientName = styled.p`
  margin: 0;
  font-weight: 600;
  font-size: 15px;
  color: ${props => props.theme.darkMode ? '#e0e0e0' : '#000'};
`;

const MessageText = styled.span`
  font-size: 14px;
  color: ${props => 
    props.hasUnread 
      ? (props.theme.darkMode ? '#e0e0e0' : '#000')
      : (props.theme.darkMode ? '#8696a0' : '#667781')
  };
  font-weight: ${props => props.hasUnread ? '600' : '400'};
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

